@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100%;
  background: #fff;
  color: #000;
  font-family: 'Open Sans', sans-serif;
}

.App {
  margin-top: 50px;
}

.container {
  max-width: 1240px;
  width: 1240px;
  margin: 5% 5%;
  margin-bottom: 50px;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, .25);
  position: relative;
  z-index: 1;
  border-radius: 12px;
  background-color: #fff;
}

.no-margin {
  margin: 0;
}

@media screen and (max-width: 768px) {
  .container {
    width: 100%;
  }

  .App {
    margin-top: 10px;
    overflow-x: hidden;
  }
}
.search-component {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 5%;
}

.search {
    padding: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    max-width: 500px;
    overflow-x: hidden;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    font-family: 'Open Sans', sans-serif;
}

.search .flex-item:nth-child(1) {
    width: 100%;
}

.search__input {
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 30px;
    max-width: 200px;
    text-align: center;
    margin-right: 1%;
    box-shadow: none;
    border-radius: 12px;
    padding: 1rem 3rem;
    background-color: #fff;
    color: #4e4c4a;
    border: 1px solid #4e4c4a;
    font-size: 20px;
    font-weight: bolder;
    letter-spacing: 0.1rem;
    font-family: 'Open Sans', sans-serif;
}

.search__input:focus {
    background: #fff;
    box-shadow: 0 0 5px#085078;
    border: 1px solid#085078;
    outline: 0;
}

.search__button {
    padding: 1rem 3rem;
    text-align: center;
    border-radius: 12px;
    color: #fff;
    letter-spacing: 0.1rem;
    min-height: 65px;
    cursor: pointer;
    text-decoration: none;
    -webkit-appearance: none;
            appearance: none;
    font-size: 20px;
    font-weight: bold;
    border: 1px solid #085078;
    background: #085078;
    font-family: 'Open Sans', sans-serif;
}

@media screen and (max-width: 768px) {

    .search__input {
        margin-bottom: 5%;
        margin-right: auto;
        max-width: 100%;
    }

    .search__button {
        -webkit-flex: 1 1;
                flex: 1 1;
    }
}
.centers {
    display: -webkit-flex;
    display: flex;
    margin: 0 3%;
    -webkit-flex-direction: row;
            flex-direction: row;
    overflow: hidden;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.center {
    display: -webkit-flex;
    display: flex;
    overflow: hidden;
    padding: 2% 5% 2% 2%;
    min-height: 200px;
    width: 300px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: auto auto 4% auto;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, .25);
    position: relative;
    z-index: 1;
    border-radius: 12px;
    background-color: #fff;
}

.result-count {
    box-shadow: none;
    margin: auto;
    margin-bottom: 3%;
    font-weight: normal;
}

.center__title-primary {
    font-size: 1.8rem;
    font-weight: bolder;
}

.center__title-secondary {
    font-size: 1.1rem;
    font-weight: normal;
}

.center__tiles {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.center__tiles>.tile {
    margin-left: 0;
}

.card__age-limit {
    top: 0;
    right: 0;
    text-align: right;
    padding: 2% 5% 2% 5%;
    position: absolute;
    font-size: 0.8rem;
    color: #fff;
    background: #085078;
    border-start-end-radius: 12px;
}


@media screen and (max-width: 768px) {
    .centers {
        width: 100vw;
        margin: 0 auto;
        margin-bottom: 50%;
    }

    .center {
        width: 100%;
        margin: 1% 5%;
        padding: 2% 15% 2% 5%;
    }

    .card__age-limit {
        padding: 2% 3% 2% 4%;
        font-size: 0.8rem;
    }

    .result-count {
        margin-left: 5%;
    }
}
.tile {
    padding: 0.4rem 0.9rem;
    font-size: 0.7rem;
    display: -webkit-flex;
    display: flex;
    border-radius: 5px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 5%;
    -webkit-justify-content: center;
            justify-content: center;
    color: #fff;
    background: #085078;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #85D8CE, #085078);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

